import React, { useEffect, useState } from "react";
import Layout from "./layout";
import HeaderTwo from "./header/header-two";
import { graphql } from 'gatsby'
import Loader from '../template/Card'
import { Container, Row, Col } from "react-bootstrap";
import { filters } from '../helpers/filter'
const HomeTwo = ({ data, location, list: masterList, mask, query, title }) => {

  const [filterList, setFilterList] = useState([])
  const [category, setCategory] = useState("All");
  useEffect(() => {
    setFilterList(masterList
      .filter(e => filters[category] ? filters[category](e) : false)
      .filter(e => e.number.includes(query)))
  }, [query, category])

  return (
    <div className="container">
      <Row>
        <Col lg={12} className="mb-10 text-center">

          {
            [
              "All",
              'I Ching',
              'Huat',
              'ABAB',
              'AAA'
            ].map(b => <span style={{ cursor: 'pointer', fontSize: '24px' }}><a onClick={() => setCategory(b)} class="page-link badge badge-secondary">{b} </a></span>)
          }
        </Col>

      </Row>

      <Loader
        title={title}
        items={mask ? filterList.map(e => ({ number: 'xxxx' + e.number.slice(4) })) : filterList}
      />
    </div>
  );
};

export default HomeTwo;



